<template src="./FileManagerTemplate.vue"></template>
<script>
import {
    login,
    uploadFile,
    fetchFiles,
    deleteFile,
    deleteFolder,
    checkSession,
    logout,
    createFolder,
    fetchUserAndUpdates,
    saveUpdate,
    renameItem,
    moveFile,
    
} from "@/services/api";
import { marked } from "marked";
import axios from "axios";
import * as XLSX from 'xlsx';

export default {
    data() {
        return {
            username: "",
            password: "",
            loggedIn: false,
            loginError: "",
            files: [],
            folders: [],
            allFolders: [],
            filteredFiles: [],
            selectedFile: null,
            uploadMessage: "",
            folderMessage: "",
            previewUrl: "",
            previewContent: '',   // TXT„Éï„Ç°„Ç§„É´„Å™„Å©„ÅÆ„ÉÜ„Ç≠„Çπ„ÉàÂÜÖÂÆπ
            downloadUrl: "https://sharefiles.ddns.net/download",
            selectedCategory: "all",
            currentFolder: "",
            selectedUploadFolder: "",
            newFolderName: "",
            uploadProgress: -1,
            showMoveModal: false,
            selectedFileForMove: "",
            selectedMoveFolder: "",
            showRenameModal: false,
            renameItemPath: '',
            renameOriginalName: '',
            renameNewName: '',
            renameType: '',
            showNewYear: false, // ÊºîÂá∫„ÅÆË°®Á§∫„Éï„É©„Ç∞
            showExcelModal: false, // Excel„Éó„É¨„Éì„É•„ÉºÁî®
            excelData: [], // Excel„Éï„Ç°„Ç§„É´„ÅÆ„Éá„Éº„Çø
            excelColumns: [], // Excel„Éï„Ç°„Ç§„É´„ÅÆ„Ç´„É©„É†
            sheets: {}, // „Ç∑„Éº„Éà„Éá„Éº„Çø„Çí‰øùÊåÅ
            selectedSheet: "", // ÁèæÂú®ÈÅ∏Êäû„Åï„Çå„Å¶„ÅÑ„Çã„Ç∑„Éº„Éà
            updateContent: "",
            isAdmin: false,
            isEditMode: false,
            renderedHtml: "",
            showEditModal: false, // Á∑®ÈõÜ„É¢„Éº„ÉÄ„É´Ë°®Á§∫„Éï„É©„Ç∞
            editFileContent: "", // Á∑®ÈõÜ‰∏≠„ÅÆ„Éï„Ç°„Ç§„É´ÂÜÖÂÆπ
            editFileName: "", // Á∑®ÈõÜ‰∏≠„ÅÆ„Éï„Ç°„Ç§„É´Âêç
            editFilePath: "", // Á∑®ÈõÜ‰∏≠„ÅÆ„Éï„Ç°„Ç§„É´„Éë„Çπ
        };
    },

    async mounted() {
        try {
            this.loggedIn = false;
            this.isAdmin = false;

            const response = await this.checkSession();
            if (response.loggedIn) {
                this.loggedIn = true;
                this.username = response.username;

                if (!this.currentFolder) {
                    this.currentFolder = "";
                }
                await this.fetchFiles(this.currentFolder);

                this.allFolders = await this.getAllFolders();
            }

            // New Year ÊºîÂá∫
            const today = new Date();
            if (today.getMonth() === 0 && today.getDate() === 1) {
                this.showNewYear = true;
                console.log("New Year Event Triggered:", this.showNewYear);

                setTimeout(() => {
                    this.showNewYear = false;
                }, 10000);
            } else {
                console.log("New Year Event Not Triggered. Current Date:", today);
            }

            if (this.loggedIn) {
                await this.fetchUserAndUpdates();
            }
        } catch (error) {
            console.error("Session check failed or API error occurred:", error);
            this.isAdmin = false;
            this.loggedIn = false;
        }
    },

    methods: {

        skipNewYear() {
            this.showNewYear = false;
            console.log("New Year Event Manually Skipped");
        },

        async login() {
            try {
                await login(this.username, this.password);
                this.loggedIn = true;
                this.loginError = "";
                await this.fetchFiles();
                await this.fetchUserAndUpdates();
            } catch (error) {
                this.loginError = error.response?.data?.message || "Login failed.";
            }
        },

        async fetchUserAndUpdates() {
            try {
                const { user, updates } = await fetchUserAndUpdates();
                this.isAdmin = user.role === "admin";

                if (updates) {
                    this.updateContent = updates;
                    await this.renderMarkdown();
                } else {
                    this.updateContent = "No updates available.";
                    this.renderedHtml = "No updates available.";
                }
            } catch (error) {
                console.error("Error fetching user or updates:", error);
                this.isAdmin = false;
                this.updateContent = "No updates available.";
                this.renderedHtml = "No updates available.";
            }
        },

        async checkSession() {
            try {
                const response = await axios.get("/session");
                return response.data;
            } catch (error) {
                console.error("Session Check Failed:", error.response?.data || error);
                return { loggedIn: false };
            }
        },

        toggleEditMode() {
            if (this.isAdmin) {
                this.isEditMode = !this.isEditMode;
            } else {
                alert("Ê®©Èôê„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ");
            }
        },

        async saveUpdate() {
            try {
                await saveUpdate(this.updateContent);
                alert("‰øùÂ≠ò„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ");
                this.isEditMode = false;
            } catch (error) {
                console.error("Error saving update:", error);
                alert("‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ„ÇÇ„ÅÜ‰∏ÄÂ∫¶Ë©¶„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
            }
        },

        async openEditModal(filePath, fileName) {
            try {
                const response = await axios.get(`/download/${encodeURIComponent(filePath)}`, {
                    responseType: "text",
                });
                this.editFileContent = response.data;
                this.editFileName = fileName;
                this.editFilePath = filePath;
                this.showEditModal = true;
            } catch (error) {
                console.error("Failed to load file content for editing:", error);
                alert("„Éï„Ç°„Ç§„É´„ÅÆË™≠„ÅøËæº„Åø„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        cancelEdit() {
            this.showEditModal = false;
            this.editFileContent = "";
            this.editFileName = "";
            this.editFilePath = "";
        },

        async saveFile() {
            try {
                await axios.post("/api/save-file", {
                    path: this.editFilePath,
                    content: this.editFileContent,
                });
                alert("„Éï„Ç°„Ç§„É´„Åå‰øùÂ≠ò„Åï„Çå„Åæ„Åó„Åü„ÄÇ");
                this.cancelEdit();
            } catch (error) {
                console.error("Failed to save file:", error);
                alert("„Éï„Ç°„Ç§„É´„ÅÆ‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        async logout() {
            try {
                await axios.post('/logout');
                // „É≠„Ç∞„Ç§„É≥Áä∂ÊÖã„Å®Ê®©ÈôêÊÉÖÂ†±„ÇíÂÆåÂÖ®„É™„Çª„ÉÉ„Éà
                this.loggedIn = false;
                this.username = "";
                this.isAdmin = false;
                this.isEditMode = false;
                this.updateContent = "";
                this.previewUrl = "";
                this.selectedFile = null;
                this.folders = [];
                this.files = [];
                this.allFolders = [];
                this.uploadMessage = "";
                this.folderMessage = "";
                this.selectedCategory = "all";
                this.currentFolder = "";
                this.selectedUploadFolder = "";
                this.selectedFileForMove = "";
                this.selectedMoveFolder = "";
                this.uploadProgress = -1;
                this.showExcelModal = false;
                this.excelData = [];
                this.excelColumns = [];
            } catch (error) {
                console.error("Logout failed:", error);
            }
        },
        watch: {
            loggedIn(newVal) {
                if (newVal) {
                    this.fetchUserAndUpdates();
                } else {
                    this.isAdmin = false;
                    this.updateContent = "No updates available.";
                    this.renderMarkdown();
                }
            },
        },

        async fetchFiles(folder = "") {
            try {
                const response = await fetchFiles(folder);
                this.folders = response.data.folders || [];
                this.files = {
                    images: response.data.files.filter(file => this.isImage(file.name)),
                    videos: response.data.files.filter(file => this.isVideo(file.name)),
                    audio: response.data.files.filter(file => this.isAudio(file.name)),
                    others: response.data.files.filter(file => !(
                        this.isImage(file.name) ||
                        this.isVideo(file.name) ||
                        this.isAudio(file.name)
                    )),
                };
                this.currentFolder = folder;
                this.filterFiles();
            } catch (error) {
                console.error("Failed to fetch files:", error);
                this.folders = [];
                this.files = { images: [], videos: [], audio: [], others: [] };
                this.filteredFiles = [];
            }
        },

        async createFolder() {
            if (!this.newFolderName) {
                this.folderMessage = "„Éï„Ç©„É´„ÉÄÂêç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ";
                this.clearMessageAfterDelay("folderMessage");
                return;
            }

            try {
                const response = await createFolder({
                    foldername: this.newFolderName,
                    base_folder: this.currentFolder || "",
                });

                this.folderMessage = response.message;
                await this.fetchFiles(this.currentFolder);
                this.allFolders = await this.getAllFolders();

                this.clearMessageAfterDelay("folderMessage");
            } catch (error) {
                if (error.response && error.response.status === 409) {
                    this.folderMessage = `Folder '${this.newFolderName}' already exists.`;
                } else {
                    this.folderMessage = "„Éï„Ç©„É´„ÉÄ„ÅÆ‰ΩúÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ";
                }
                this.clearMessageAfterDelay("folderMessage");
            }
        },

        clearMessageAfterDelay(messageField, delay = 5000) {
            setTimeout(() => {
                this[messageField] = "";
            }, delay);
        },

        goToParent() {
            if (this.currentFolder) {
                const parentFolder = this.currentFolder.split("/").slice(0, -1).join("/");
                this.fetchFiles(parentFolder);
            }
        },

        async uploadFile() {
            if (!this.selectedFile) {
                this.uploadMessage = "Âàù„ÇÅ„Å´„Éï„Ç°„Ç§„É´„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ";
                return;
            }

            this.uploadProgress = 0;

            try {
                const response = await uploadFile(
                    this.selectedFile,
                    this.selectedUploadFolder || "",
                    (progress) => {
                        this.uploadProgress = progress;
                        console.log(`Upload progress: ${progress}%`);
                    }
                );

                this.uploadMessage = response.data.message || "„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ";
                this.selectedFile = null;
                this.$refs.fileInput.value = "";
                this.selectedUploadFolder = "";
                this.uploadProgress = -1;
                await this.fetchFiles(this.currentFolder);

                setTimeout(() => {
                    this.uploadMessage = "";
                }, 5000);
            } catch (error) {
                console.error("Error during file upload:", error);
                this.uploadMessage =
                    error.response?.data?.message || "„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ";
                this.uploadProgress = -1;
            }
        },

        async deleteFolder(folderPath) {
            if (!confirm(`"${folderPath}" „ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü`)) {
                return;
            }

            this.folders = this.folders.filter(folder => folder.path !== folderPath);
            try {
                const response = await deleteFolder(folderPath);
                alert(response.message);

                await this.fetchFiles(this.currentFolder);
                this.allFolders = await this.getAllFolders();
            } catch (error) {
                console.error("Failed to delete folder:", error);
                alert("ÂâäÈô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        resetTableStyles() {
            const table = document.querySelector("#excelTable");
            if (table) {
                const headers = table.querySelectorAll("th");
                headers.forEach((header) => {
                    header.style.width = "";
                });

                const rows = table.querySelectorAll("tr");
                rows.forEach((row) => {
                    row.style.height = "";
                });
            }
        },

        async previewExcel(filePath) {
            try {
                this.resetTableStyles();

                const response = await axios.get(`/download/${encodeURIComponent(filePath)}`, {
                    responseType: "blob",
                });

                const fileData = await response.data.arrayBuffer();
                const workbook = XLSX.read(fileData, { type: "array" });

                this.sheets = workbook.SheetNames.reduce((acc, sheetName) => {
                    const sheetData = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName], { header: 1 });
                    acc[sheetName] = sheetData;
                    return acc;
                }, {});

                this.selectedSheet = workbook.SheetNames[0];
                this.updateExcelTable();

                this.showExcelModal = true;
            } catch (error) {
                console.error("Excel„Éï„Ç°„Ç§„É´„ÅÆ„Éó„É¨„Éì„É•„Éº„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ:", error);
                alert("Excel„Éï„Ç°„Ç§„É´„ÅÆ„Éó„É¨„Éì„É•„Éº„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        //Âàó„Éò„ÉÉ„ÉÄ„ÉºÁîüÊàêÁî®„É°„ÇΩ„ÉÉ„Éâ
        generateExcelColumns(columnsCount) {
            const columns = [];
            for (let i = 0; i < columnsCount; i++) {
                let columnName = "";
                let col = i;
                do {
                    columnName = String.fromCharCode((col % 26) + 65) + columnName;
                    col = Math.floor(col / 26) - 1;
                } while (col >= 0);
                columns.push(columnName);
            }
            return columns;
        },

        updateExcelTable() {
            if (this.selectedSheet && this.sheets[this.selectedSheet]) {
                const sheetData = this.sheets[this.selectedSheet];
                const rowsCount = Math.max(sheetData.length, 20); // „Éá„Éï„Ç©„É´„ÉàË°åÊï∞„Çí20„Å´Ë®≠ÂÆö
                const columnsCount = Math.max(
                    ...sheetData.map(row => row.length),
                    17 // ÊúÄ‰ΩéQÂàóÂàÜ„ÇíÁ¢∫‰øù
                );

                this.excelColumns = this.generateExcelColumns(columnsCount);
                this.excelData = Array.from({ length: rowsCount }, (_, rowIndex) => {
                    return Array.from({ length: columnsCount }, (_, colIndex) => {
                        return sheetData[rowIndex]?.[colIndex] || ""; // „Éá„Éº„Çø„Åå„Å™„ÅÑÂ†¥Âêà„ÅØÁ©∫ÁôΩ„ÇíÂüã„ÇÅ„Çã
                    });
                });

                setTimeout(() => this.enableResizableCells(), 0);
            }
        },

        enableResizableCells() {
            const table = document.querySelector("#excelTable");
            if (!table) return;

            const headers = table.querySelectorAll("th");
            headers.forEach((header) => {
                const resizer = document.createElement("div");
                resizer.className = "resize-handle-horizontal";
                resizer.addEventListener("mousedown", this.startColumnResize);
                header.appendChild(resizer);
            });

            const rows = table.querySelectorAll("tr");
            rows.forEach((row) => {
                const rowNumberCell = row.querySelector(".row-number");
                if (rowNumberCell) {
                    const resizer = document.createElement("div");
                    resizer.className = "resize-handle-vertical";
                    resizer.addEventListener("mousedown", this.startRowResize);
                    rowNumberCell.appendChild(resizer);
                }
            });

            const cells = table.querySelectorAll("td");
            cells.forEach((cell) => {
                const horizontalResizer = document.createElement("div");
                horizontalResizer.className = "resize-handle-horizontal";
                horizontalResizer.addEventListener("mousedown", (event) =>
                    this.startColumnResize(event, cell)
                );
                cell.appendChild(horizontalResizer);

                const verticalResizer = document.createElement("div");
                verticalResizer.className = "resize-handle-vertical";
                verticalResizer.addEventListener("mousedown", (event) =>
                    this.startRowResize(event, cell)
                );
                cell.appendChild(verticalResizer);
            });
        },

        startColumnResize(event, targetCell = null) {
            const th = targetCell
                ? targetCell
                : event.target.parentElement;
            const table = th.closest("table");
            const startX = event.pageX;
            const columnIndex = Array.from(th.parentElement.children).indexOf(th);
            const startWidth = th.offsetWidth;

            let animationFrameId;

            document.body.classList.add("dragging");

            const mouseMoveHandler = (e) => {
                const newWidth = Math.max(startWidth + (e.pageX - startX), 30);
                if (!animationFrameId) {
                    animationFrameId = requestAnimationFrame(() => {
                        table.querySelectorAll(
                            `td:nth-child(${columnIndex + 1}), th:nth-child(${columnIndex + 1})`
                        ).forEach((cell) => (cell.style.width = `${newWidth}px`));
                        animationFrameId = null;
                    });
                }
            };

            const mouseUpHandler = () => {
                document.body.classList.remove("dragging");
                document.removeEventListener("mousemove", mouseMoveHandler);
                document.removeEventListener("mouseup", mouseUpHandler);
                if (animationFrameId) {
                    cancelAnimationFrame(animationFrameId);
                }
            };

            document.addEventListener("mousemove", mouseMoveHandler);
            document.addEventListener("mouseup", mouseUpHandler);
        },

        startRowResize(event, targetCell = null) {
            const row = targetCell
                ? targetCell.parentElement
                : event.target.closest("tr");
            const table = row.closest("table");
            const startY = event.pageY;
            const rowIndex = Array.from(row.parentElement.children).indexOf(row);
            const startHeight = row.offsetHeight;

            let animationFrameId;

            document.body.classList.add("dragging");

            const mouseMoveHandler = (e) => {
                const newHeight = Math.max(20, startHeight + (e.pageY - startY));
                if (!animationFrameId) {
                    animationFrameId = requestAnimationFrame(() => {
                        table.querySelectorAll(
                            `tr:nth-child(${rowIndex + 1}) td, tr:nth-child(${rowIndex + 1}) th`
                        ).forEach((cell) => (cell.style.height = `${newHeight}px`));
                        animationFrameId = null;
                    });
                }
            };

            const mouseUpHandler = () => {
                document.body.classList.remove("dragging");
                document.removeEventListener("mousemove", mouseMoveHandler);
                document.removeEventListener("mouseup", mouseUpHandler);
                if (animationFrameId) {
                    cancelAnimationFrame(animationFrameId);
                }
            };

            document.addEventListener("mousemove", mouseMoveHandler);
            document.addEventListener("mouseup", mouseUpHandler);
        },

        async previewFile(filePath) {
            try {
                const encodedPath = encodeURIComponent(filePath.replace(/\\/g, "/"));
                this.previewUrl = `${this.downloadUrl}/${encodedPath}`;
                console.log(`Sending preview request to: ${this.previewUrl}`);

                if (this.isMarkdown(filePath)) {
                    await this.previewMarkdown(filePath);
                } else if (this.isExcel(filePath)) {
                    await this.previewExcel(filePath);
                } else if (this.isText(filePath)) {
                    const response = await axios.get(this.previewUrl, { responseType: "text" });
                    this.previewContent = this.embedVideos(response.data);
                } else {
                    const response = await axios.get(this.previewUrl);
                    console.log("Preview response:", response);
                }
            } catch (error) {
                console.error("Failed to preview file:", error);
                alert(`„Éó„É¨„Éì„É•„Éº‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ: ${error.message}`);
            }
        },

        renderMarkdown: async function () {
            try {
                if (!this.updateContent || this.updateContent.trim() === "") {
                    this.renderedHtml = "";
                    return;
                }

                // ÂãïÁîª„É™„É≥„ÇØ„ÇíÂüã„ÇÅËæº„ÅøÂΩ¢Âºè„Å´Â§âÊèõ
                const processedContent = this.embedVideos(this.updateContent);

                // `marked()` „Å´Ê∏°„ÅôÂâç„Å´ `string` Âûã„Åß„ÅÇ„Çã„Åì„Å®„Çí‰øùË®º
                const safeContent = typeof processedContent === "string" ? processedContent : "";


                // „Ç´„Çπ„Çø„É†„É¨„É≥„ÉÄ„É©„Éº„ÇíË®≠ÂÆö
                const renderer = new marked.Renderer();
                renderer.list = function (body, ordered) {
                    const type = ordered ? "ol" : "ul";
                    return `<${type}>${body}</${type}>`;
                };

                // marked„ÅÆ„Ç™„Éó„Ç∑„Éß„É≥„ÇíË®≠ÂÆö
                marked.setOptions({
                    breaks: true, // ÊîπË°å„Çí `<br>` „Å®„Åó„Å¶Âá¶ÁêÜ
                    gfm: true,    // GitHub Flavored Markdown„ÇíÊúâÂäπÂåñ
                    tables: true, // „ÉÜ„Éº„Éñ„É´ÊßãÊñá„ÇíÊúâÂäπÂåñ
                    renderer,     // „Ç´„Çπ„Çø„É†„É¨„É≥„ÉÄ„É©„Éº„ÇíË®≠ÂÆö
                });

                this.renderedHtml = marked(safeContent);
            } catch (error) {
                console.error("renderMarkdown: Error rendering Markdown content:", error);
            }
        },

        async previewMarkdown(filePath) {
            try {
                this.previewContent = "";
                this.markdownHtml = "";

                // „Éï„Ç°„Ç§„É´ÂÜÖÂÆπ„ÇíÂèñÂæó
                const response = await axios.get(`/download/${encodeURIComponent(filePath)}`, {
                    responseType: "text",
                });

                // `marked()` „Å´Ê∏°„ÅôÂâç„Å´ `string` Âûã„Åß„ÅÇ„Çã„Åì„Å®„Çí‰øùË®º
                const safeContent = typeof response.data === "string" ? response.data : "";

                const renderer = new marked.Renderer();

                // „Ç´„Çπ„Çø„É†„É™„É≥„ÇØ„É¨„É≥„ÉÄ„É©„Éº„ÇíË®≠ÂÆö
                renderer.link = function (href, title, text) {
                    const hrefString = typeof href === "string" ? href : href?.href || "";

                    if (hrefString) {
                        // YouTube„É™„É≥„ÇØ„ÅÆÊ≠£Ë¶èË°®Áèæ
                        const youtubeRegex = /(https?:\/\/(?:www\.)?youtube\.com\/watch\?v=|https?:\/\/youtu\.be\/)([\w-]+)/;
                        const youtubeMatch = hrefString.match(youtubeRegex);

                        if (youtubeMatch) {
                            const videoId = youtubeMatch[2];
                            return `
                    <div class="youtube-container">
                        <iframe width="560" height="315" src="https://www.youtube.com/embed/${videoId}" frameborder="0" allowfullscreen></iframe>
                    </div>`;
                        }

                        // „Éã„Ç≥„Éã„Ç≥ÂãïÁîª„É™„É≥„ÇØ„ÅÆÊ≠£Ë¶èË°®Áèæ
                        const nicovideoRegex = /(https?:\/\/(?:www\.)?nicovideo\.jp\/watch\/)([a-zA-Z0-9]+)/;
                        const nicovideoMatch = hrefString.match(nicovideoRegex);

                        if (nicovideoMatch) {
                            const videoId = nicovideoMatch[2];
                            return `
                    <div class="nicovideo-container">
                        <iframe width="560" height="315" src="https://embed.nicovideo.jp/watch/${videoId}" frameborder="0" allowfullscreen></iframe>
                    </div>`;
                        }
                    }

                    // ÈÄöÂ∏∏„ÅÆ„É™„É≥„ÇØ„Å®„Åó„Å¶Ëøî„Åô
                    return `<a href="${hrefString}" title="${title || ""}">${text || hrefString}</a>`;
                };

                marked.setOptions({
                    breaks: true, // ÊîπË°å„Çí `<br>` „Å´Â§âÊèõ
                    gfm: true,    // GitHub Flavored Markdown„ÇíÊúâÂäπÂåñ
                    tables: true, // „ÉÜ„Éº„Éñ„É´ÊßãÊñá„ÇíÊúâÂäπÂåñ
                    renderer,     // „Ç´„Çπ„Çø„É†„É¨„É≥„ÉÄ„É©„Éº„ÇíË®≠ÂÆö
                });

                // `marked()` „Å´Ê∏°„ÅôÂâç„Å´ `string` Âûã„Åß„ÅÇ„Çã„Åì„Å®„Çí‰øùË®º
                this.markdownHtml = marked(safeContent);
                this.previewUrl = filePath;
            } catch (error) {
                console.error("previewMarkdown: Error previewing Markdown file:", error);
                alert("Markdown „Éó„É¨„Éì„É•„Éº„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        embedVideos(content) {
            // YouTube„ÅÆÊ≠£Ë¶èË°®Áèæ
            const youtubeRegex = /(https?:\/\/(?:www\.)?(?:youtube\.com\/(?:watch\?v=|live\/)|youtu\.be\/))([\w-]+)/g;

            // „Éã„Ç≥„Éã„Ç≥ÂãïÁîª„ÅÆÊ≠£Ë¶èË°®Áèæ
            const nicovideoRegex = /(https?:\/\/(?:www\.|)nicovideo\.jp\/watch\/)([a-z0-9]+)/g;

            // YouTubeÂüã„ÇÅËæº„Åø
            content = content.replace(youtubeRegex, (_, __, videoId) => {
                return `
        <div class="video-container">
            <iframe 
                src="https://www.youtube.com/embed/${videoId}" 
                frameborder="0" 
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                allowfullscreen>
            </iframe>
        </div>`;
            });

            // „Éã„Ç≥„Éã„Ç≥ÂãïÁîªÂüã„ÇÅËæº„Åø
            content = content.replace(nicovideoRegex, (_, __, videoId) => {
                return `
        <div class="video-container">
            <iframe 
                src="https://embed.nicovideo.jp/watch/${videoId}" 
                frameborder="0" 
                allowfullscreen>
            </iframe>
        </div>`;
            });

            return content;
        },

        downloadFile(filePath) {
            const encodedPath = encodeURIComponent(filePath.replace(/\\/g, "/"));
            const downloadUrl = `${this.downloadUrl}/${encodedPath}`;
            window.open(downloadUrl, '_blank');
        },

        closePreview() {
            this.previewUrl = "";
            this.previewContent = '';
            this.showExcelModal = false;
            this.excelData = [];
            this.excelColumns = [];
        },

        confirmDelete(file) {
            if (confirm(`${file}„ÇíÂâäÈô§„Åó„Å¶„Çà„Çç„Åó„ÅÑ„Åß„Åô„ÅãÔºü`)) {
                this.deleteFile(file);
            }
        },

        async getAllFolders(baseFolder = "") {
            try {
                const response = await fetchFiles(baseFolder);
                const currentFolders = response.data.folders || [];
                let allFolders = currentFolders.map(folder =>
                    baseFolder ? `${baseFolder}/${folder.name}` : folder.name
                );

                for (const folder of currentFolders) {
                    const subFolderPath = baseFolder ? `${baseFolder}/${folder.name}` : folder.name;
                    const subFolders = await this.getAllFolders(subFolderPath);
                    allFolders = allFolders.concat(subFolders);
                }

                return allFolders;
            } catch (error) {
                console.error("Failed to fetch all folders:", error);
                alert("‰∏ÄË¶ß„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇÊõ¥Êñ∞„ÇíË©¶„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
                return [];
            }
        },

        openMoveModal(filePath) {
            this.selectedFileForMove = filePath;
            this.showMoveModal = true;
        },
        cancelMove() {
            this.selectedFileForMove = "";
            this.selectedMoveFolder = "";
            this.showMoveModal = false;
            console.log("Move modal canceled and state cleared.");
        },

        openRenameModal(itemPath, originalName, type) {
            this.renameItemPath = itemPath;
            this.renameType = type;
            this.renameOriginalName = originalName;

            if (type === "file") {
                this.renameNewName = originalName.replace(/\.[^/.]+$/, "");
            } else {
                this.renameNewName = originalName;
            }

            this.showRenameModal = true;
        },

        cancelRename() {
            this.renameItemPath = '';
            this.renameOriginalName = '';
            this.renameNewName = '';
            this.renameType = '';
            this.showRenameModal = false;
        },

        async renameItem() {
            try {
                const newName = this.renameType === "file"
                    ? `${this.renameNewName}.${this.renameOriginalName.split(".").pop()}`
                    : this.renameNewName;

                await renameItem(this.renameItemPath, newName, this.renameType);
                alert("„É™„Éç„Éº„É†„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ");
                this.cancelRename();
                this.fetchFiles(this.currentFolder);
            } catch (error) {
                console.error("Error renaming item:", error);
                alert("„É™„Éç„Éº„É†„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        async moveFile() {
            try {
                await moveFile(this.selectedFileForMove, this.selectedMoveFolder);
                alert("ÁßªÂãï„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ");
                this.cancelMove();
                this.fetchFiles(this.currentFolder);
            } catch (error) {
                console.error("Error moving file:", error);
                alert("ÁßªÂãï„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        onFileChange(event) {
            this.selectedFile = event.target.files[0];
            if (this.selectedFile) {
                console.log("Selected file:", this.selectedFile.name);
            }
        },
        async deleteFile(filePath) {
            try {
                const response = await deleteFile(filePath);
                alert(response.message);
                await this.fetchFiles(this.currentFolder);
            } catch (error) {
                console.error("Failed to delete file:", error);
                alert("ÂâäÈô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
            }
        },

        filterFiles() {
            if (this.selectedCategory === "all") {
                this.filteredFiles = Object.values(this.files).flat();
            } else {
                this.filteredFiles = this.files[this.selectedCategory] || [];
            }
        },
        isImage(fileUrl) {
            return /\.(jpg|jpeg|png|gif|bmp|webp)$/i.test(fileUrl);
        },
        isVideo(fileUrl) {
            return /\.(mp4|webm|ogg)$/i.test(fileUrl);
        },
        isAudio(fileUrl) {
            return /\.(mp3|wav|flac|ogg)$/i.test(fileUrl);
        },
        isText(fileUrl) {
            return /\.txt$/i.test(fileUrl);
        },
        isMarkdown(fileUrl) {
            return /\.(md|markdown)$/i.test(fileUrl);
        },
        isExcel(fileUrl) {
            return /\.(xlsx|xls)$/i.test(fileUrl);
        },
        isPDF(fileUrl) {
            return /\.pdf$/i.test(fileUrl);
        },
    },
};
</script>
