import axios from "axios";

// „Ç∞„É≠„Éº„Éê„É´IP„ÅÆË®≠ÂÆö„ÇíHTTPS„Éâ„É°„Ç§„É≥„Å´Áµ±‰∏Ä
axios.defaults.baseURL = "https://sharefiles.ddns.net";

// API„Ç®„ÇØ„Çπ„Éù„Éº„Éà
export const login = async (username, password) => {
  const response = await axios.post("/login", { username, password });
  if (response.status === 200) {
    return response.data;
  }
  throw new Error(response.data.message || "Login failed.");
};

export const checkSession = () => axios.get("/session");

export const logout = () => axios.post("/logout");

export const uploadFile = (file, folder, onUploadProgress) => {
  const formData = new FormData();
  formData.append("file", file);
  if (folder) {
    formData.append("folder", folder);
  }
  return axios.post("/upload", formData, {
    onUploadProgress: (progressEvent) => {
      const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
      if (onUploadProgress) {
        onUploadProgress(percentCompleted);
      }
    },
  });
};

export const fetchFiles = (folder = "") =>
  axios.get(`/files${folder ? `?folder=${encodeURIComponent(folder)}` : ""}`);

export async function deleteFile(filePath) {
  try {
    const response = await axios.delete("/delete", {
      data: { path: filePath }, // JSONÂΩ¢Âºè„Åßpath„ÇíÈÄÅ‰ø°
    });
    return response.data;
  } catch (error) {
    console.error("Failed to delete file:", error);
    throw error;
  }
}

export async function deleteFolder(folderPath) {
  try {
    const response = await axios.delete("/delete", {
      data: { path: folderPath }, // „Éï„Ç©„É´„ÉÄ„Éë„Çπ„ÇíJSON„ÅßÈÄÅ‰ø°
    });
    return response.data;
  } catch (error) {
    console.error("Failed to delete folder:", error);
    throw error;
  }
}

export const createFolder = (data) =>
  axios.post(`/create-folder`, data);

export const fetchUserAndUpdates = async () => {
  const userResponse = await axios.get("/api/user");
  const updateResponse = await axios.get("/api/system-updates");
  return {
    user: userResponse.data,
    updates: updateResponse.data.content,
  };
};

export const saveUpdate = async (content) => {
  const response = await axios.post("/api/system-updates", { content });
  return response.data;
};

export const renameItem = async (path, newName, type) => {
  const response = await axios.post("/rename", { path, new_name: newName, type });
  return response.data;
};

export const moveFile = async (srcPath, destFolder) => {
  const response = await axios.post("/move", { src_path: srcPath, dest_folder: destFolder });
  return response.data;
};
